import React from "react";
import { NavLink } from "react-router-dom";
import { useShoppingCartStore } from "../../store/shoopingCartStore";

const MiniShoppingCart = () => {
  const { sc } = useShoppingCartStore();
  console.log('mini SHOPPING CART RENDER');
  const soppingCartNumber = sc.length;

  const navLinkClassName = (navData) => {
    return navData.isActive ? "navLink Active" : "navLink";
  };
  return (
    <NavLink
      className={(navData) => navLinkClassName(navData)}
      to="/shoppingcart"
    >
      <div>
        <div>Cart</div><div>{soppingCartNumber > 0 ? `${soppingCartNumber} Items` : ''}</div> 
      </div>
    </NavLink>
  );
};

export default React.memo(MiniShoppingCart);
