import React from "react";

import { ADD_TO_CART, REMOVE_FROM_CART, REMOVE_ITEM_ALL,MUST_REMOVE } from "./reducerTypes";
export const initialState = {
  shoppingCart: [],
  totalPrice: 0,
  mustRemoveItems: [],
};

export const shoppngCartReducer = (state = { ...initialState }, action) => {
  const { type, payload } = action;
  const { shoppingCart, totalPrice } = state;

  switch (type) {
    case MUST_REMOVE: {
      const { id, price } = payload;

      return { ...state };
    }
    case REMOVE_ITEM_ALL: {
      const { id, price } = payload;
      if (shoppingCart.length && shoppingCart.includes(id)) {
        let filtered = [];
        let removed = [];
        for (let i = 0; i < shoppingCart.length; i++) {
          if (shoppingCart[i] === id) {
            removed.push(shoppingCart[id]);
          } else {
            filtered.push(shoppingCart[i]);
          }
        }
        const newTotalPrice = price * removed.length;
        return {
          ...state,
          totalPrice: +totalPrice - newTotalPrice,
          shoppingCart: [...filtered],
        };
      }
    }
    case ADD_TO_CART: {
      const { id, price } = payload;
      return {
        ...state,
        totalPrice: +totalPrice + price,
        shoppingCart: [...shoppingCart, id],
      };
    }
    case REMOVE_FROM_CART: {
      const { id, price } = payload;
      if (shoppingCart.length && shoppingCart.includes(id)) {
        let updatedArr = [...shoppingCart];

        const index = updatedArr.lastIndexOf(id);
        if (index > -1) {
          updatedArr.splice(index, 1);
        }
        return {
          ...state,
          totalPrice: totalPrice - price,
          shoppingCart: [...updatedArr],
        };
      } else {
        return { ...state };
      }
    }
    default: {
      throw new Error(
        `shoppngCartReducer ERROR: Unhandled type: ${action.type}`
      );
    }
  }
};

export const useShoppngCartReducer = () => [
  ...React.useReducer(shoppngCartReducer, initialState),
  initialState,
];
