import React from "react";
import ReactDOM from "react-dom";
import App from "./App";
import { CurrencyProvider } from "./store/currencyStore";
import { ShoopingCartStoreProvider } from "./store/shoopingCartStore";

ReactDOM.render(
  <React.StrictMode>
    <CurrencyProvider>
      <ShoopingCartStoreProvider>
        <App />
      </ShoopingCartStoreProvider>
    </CurrencyProvider>
  </React.StrictMode>,
  document.getElementById("root")
);
