import React, { useEffect } from "react";
import { useShoppngCartReducer } from "../reducers/shoppingCart/shoppingCartReducer";
import {
  ADD_TO_CART,
  REMOVE_FROM_CART,REMOVE_ITEM_ALL
} from "../reducers/shoppingCart/reducerTypes";
const Store = React.createContext();

export const ShoopingCartStoreProvider = ({ children }) => {
  const [state, dispatch] = useShoppngCartReducer();

  const addToCart = (id, price) => {
    dispatch({
      type: ADD_TO_CART,
      payload: { id, price },
    });

    const ls_sc = JSON.parse(localStorage.getItem("shoppingCart")) || [];
    console.log("ls_sc", ls_sc);
    const updateLS = [...ls_sc, { id, price }];
    localStorage.setItem("shoppingCart", JSON.stringify(updateLS));
  };
  const removeFromCart = (id, price) => {
    dispatch({
      type: REMOVE_FROM_CART,
      payload: { id, price },
    });
    const ls_sc = JSON.parse(localStorage.getItem("shoppingCart")) || [];
    const index = ls_sc.findIndex((x) => x.id == id);
    if (index > -1) {
      ls_sc.splice(index, 1);
      localStorage.setItem("shoppingCart", JSON.stringify(ls_sc));
    }
  };
  const removeItemAll = (id, price) => {
    dispatch({
      type: REMOVE_ITEM_ALL,
      payload: { id, price },
    });
    const ls_sc = JSON.parse(localStorage.getItem("shoppingCart")) || [];
    let filtered =[]
    filtered =  ls_sc.filter((x) => x.id !== id);
      localStorage.setItem("shoppingCart", JSON.stringify(filtered));
    
  };
  useEffect(() => {
    const ls_sc = JSON.parse(localStorage.getItem("shoppingCart")) || [];

    if (ls_sc.length) {
      ls_sc.forEach((item, idx) => {
        const { id, price } = item;
        dispatch({
          type: ADD_TO_CART,
          payload: { id, price },
        });
      });
    }
  }, []);

  const mamoizedValue = React.useMemo(
    () => ({
      sc: [...state.shoppingCart],
      addToCart,
      removeFromCart,
      removeItemAll,
      totalPrice: state.totalPrice,
    }),
    [state.shoppingCart]
  );
  return <Store.Provider value={mamoizedValue}>{children}</Store.Provider>;
};

export const useShoppingCartStore = () => React.useContext(Store);
