import React, { useEffect, useState, useRef } from "react";
import { useDeviceDetect } from "../../hooks";
import { useCurrencyStore,CURRENCIES } from "../../store/currencyStore";
import { getClasses } from "../../utils";
import { Main } from "./styles";

const CurrencyDropdown = ({ className, extraStyling }) => {

const { setCurrency, currency } = useCurrencyStore();
  
  const { isMobile } = useDeviceDetect();
  const rates = CURRENCIES;
  const listRef = useRef();
  const MainClasses = getClasses({
    ["CurrencyMain"]: true,
    [className]: !!className,
  });
  const [sortedRates, setSortedRates] = useState([...rates]);
  const [open, setOpen] = useState(false);

  const sort = () => {
    let newCurrencyArr = [...rates];
    //swap
    const selectedIdx = rates
      .map(function (e) {
        return e.code;
      })
      .indexOf(currency?.code);
    newCurrencyArr[0] = rates[selectedIdx];
    newCurrencyArr[selectedIdx] = rates[0];

    setSortedRates(newCurrencyArr);
  };
  useEffect(() => {
    sort();
  }, [currency?.code]);

  const onChange = (rateCode) => {
    setCurrency(rateCode);
    isMobile && setOpen(false);
  };

  const onMouseEnter = (e) => {
    e.preventDefault();
    e.stopPropagation();

    !isMobile && setOpen(true);
  };
  const onMouseLeave = (e) => {
    e.preventDefault();
    e.stopPropagation();
    !isMobile && setOpen(false);
  };

  const toggleOpen = (e) => {
    isMobile && !open && setOpen(true);
  };

  return (
    <Main className={MainClasses} extraStyling={extraStyling} open={open}>
      <ul
        className={"CurrencyList"}
        ref={listRef}
        onMouseEnter={onMouseEnter}
        onMouseLeave={onMouseLeave}
        onClick={toggleOpen}
      >
        {sortedRates.map((rate, idx) => {
          const clickHandle = idx === 0 ? toggleOpen : () => {};
          return (
            <li
              onClick={() => {
                onChange(rate.code);
                clickHandle();
              }}
              className="rateItem"
              key={`${rate.code}`}
            >
              <span
                className={"flag"}
                style={{
                  backgroundImage: `url(flags/${rate.code}.svg)`,
                  backgroundSize: "cover",
                }}
              />

              <span className={"label"}>{rate.code}</span>
            </li>
          );
        })}
      </ul>
    </Main>
  );
};

export default React.memo(CurrencyDropdown);
