import styled, { css } from "styled-components";

export const Main = styled.div`
  position: relative;
  width: 95%;
  height: 34px;

  .CurrencyList {
    position: absolute;
    z-index: 10;
    background: #fff;
    /* top: 2px; */
    width: 100%;
    font-size: 11px;
    letter-spacing: normal;
    box-sizing: border-box;
    border-radius: 4px;
    padding: 0;
    list-style: none;
    margin: 0;
    ${({ open }) =>
      open &&
      css`
        box-shadow: var(--headerDesktop-dropShadow);
      `}
    li {
      cursor: pointer;
      padding: 9px 0 9px 7px;
      display: none;
      width: calc(100% - 7px);
      color: var(--headerDesktop-textColor);
      :first-child {
        display: flex;
        position: relative;
        :after {
          content: "";
          position: absolute;
          color: #656565;
          right: 9px;
          top: 50%;
          transform: translateY(-50%);
          clear: both;
          transition: ease all 0.3s;
          width: 0;
          height: 0;
          border-left: 3px solid transparent;
          border-right: 3px solid transparent;
          border-top: 3px solid #656565;
        }
      }
      :hover {
        background-color: rgba(242, 242, 242, 0.3);
      }
      ${({ open }) =>
        open &&
        css`
          :not(:first-child) {
            display: flex;
          }
        `}
      span {
        display: inline-block;
        vertical-align: middle;
        background-repeat: no-repeat;
        background-size: 225px;
      }
    }
    :hover li:first-child:after {
      transform: rotate(-180deg) translateY(50%);
    }
    li:active,
    ul:active {
      background: none;
    }

    .flag {
      height: 16px;
      margin-right: 4px;
      width: 22px;
      vertical-align: middle;
    }
    .label {
      margin-top: 2px;
    }

    .USD {
      background-position: -14px -7px;
    }

    .AUD {
      background-position: -64px -7px;
    }

    .EUR {
      background-position: -39px -7px;
    }

    .GBP {
      background-position: -164px -7px;
    }

    .flag {
      height: 16px;
      margin-right: 4px;
      width: 22px;
      vertical-align: middle;
    }

    .top-menu-item {
      justify-self: center;
      position: relative;
      width: 66px;
      height: 35px;
      z-index: 1000;
      background: #fff;
    }

    ${({ extraStyling }) => extraStyling && extraStyling}
  }
`;
