import React from "react";
import { NavLink } from "react-router-dom";
import { StyledNav } from "./Nav.style";
import MiniShoppingCart from "./MiniShoppingCart";
import CurrencyDropdown from "../CurrencyDropdown/CurrencyDropdown";
const Nav = () => {
  const navLinkClassName = (navData) => {
    return navData.isActive ? "navLink Active" : "navLink";
  };

  console.log("Nav render");
  return (
    <StyledNav>
      <div className="main">
        <div className="navContent">
          <NavLink className={(navData) => navLinkClassName(navData)} to="/">
            Home
          </NavLink>
          <NavLink
            className={(navData) => navLinkClassName(navData)}
            to="/gallery"
          >
            Gallery
          </NavLink>
          <MiniShoppingCart />
        </div>
        <div className="currency">
          <CurrencyDropdown />
        </div>
      </div>
    </StyledNav>
  );
};

export default React.memo(Nav);
