import React, { useCallback, useMemo, useState } from "react";
export const CURRENCIES = [
  {
    code: "USD",
    symbol: "$",
    rate: 1,
  },
  {
    code: "EUR",
    symbol: "€",
    rate: 0.9279,
  },
  {
    code: "GBP",
    symbol: "£",
    rate: 0.7894,
  },
  {
    code: "AUD",
    symbol: "£",
    rate: 1.4655,
  },
];
const Store = React.createContext();

export const CurrencyProvider = ({ children }) => {
  const [currency, setCurrency] = useState(CURRENCIES[0]);

  const setCurrencyHandler = useCallback((code) => {
    const newCurency = CURRENCIES.find((e) => e.code === code);
    setCurrency(newCurency);
  }, []);

  const value = useMemo(
    () => ({
      name: "CurrencyProvider",
      currency,
      setCurrency: setCurrencyHandler,
    }),
    [currency]
  );
  return <Store.Provider value={value}>{children}</Store.Provider>;
};

export const useCurrencyStore = () => React.useContext(Store);
