import styled from "styled-components";

export const StyledNav = styled.nav`
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  height: 70px;
  backdrop-filter: blur(20px);
  border-bottom: 1px solid var(--borderColor);
  min-width: 845px;
  z-index:1000;
  @media (max-width: 600px) {
      padding: 0 2px;
      min-width: auto;
      backdrop-filter: none;
      background:#fff;
    }
  .main {
    padding: 0 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 100%;

    @media (max-width: 600px) {
      padding: 0 2px;
  
    }
    .navContent {
      height: 100%;
      width: 50%;
      display: flex;
      align-items: center;
      justify-content: space-between;
      @media (max-width: 600px) {
        width: 75%;
        justify-content:flex-start;

    }
      .navLink {
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-right: 10px;
        width: 150px;
        margin-top: 50%;
        text-align: center;
        align-self: flex-end;
        text-decoration: none;

        border-bottom: transparent 2px solid;
        transition: border-color 0.3s;
        @media (max-width: 600px) {
          margin-right: 0;
          width: 100px;

    }
        :hover {
          background: lightskyblue;
          border-color: var(--borderColor);
        }
        &.Active {
          border-color: var(--linkColor);
        }
      }
    }
    .currency {
      justify-self: flex-end;
      position: relative;
      width: 85px;
    }
  }
`;
